import React, { Fragment, useContext, useEffect } from 'react';

import Loginverif from '../components/Loginverif'
import Navbar from '../components/Navbar';
import Barcalendar from '../components/Barcalendar';
import { AppContext } from '../context/Statecontext';

import userimg from '../images/user.svg';


let calendar = [
    {   
        Name: "Deporte",
        rol: "prop",
        color: "red"
    },
    {   
        Name: "Cocina",
        rol: "prop",
        color: "green"
    },
    {   
        Name: "Evento ubuntu",
        rol: "inv",
        color: "orange"
    },
    {   
        Name: "Mamá",
        rol: "inv",
        color: "pink"
    }
]

const Sesion = () => {

    // ESTADO GLOBAL
    const [globalState, setGlobalState] = useContext(AppContext);


    useEffect(() =>{

        let paquete = {
            method: "GET",
            mode: "cors"
        }
        
        fetch('http://localhost:8080/Calendar', paquete)
            .then(response => response.json())
            .catch(err => console.error(err))
            .then(response => {
                console.log(response.Calendarios);
            });
    });

    return (
        <Fragment>
            <Loginverif />
            <div>
                <Navbar type = {1} img = {userimg} UserName={globalState.UserName} />
                <Barcalendar calendar = {calendar}/>
            </div>
        </Fragment>
    );

};

export default Sesion;